<template>
<nav class='navigation navigation--offcanvas' >
  <ul class="list--clean">
    <li >
      <a class="is-active" href="/">HOME</a>
    </li>
    <li>
      <a @click.prevent='changeMode' href="/votes">MLA VOTING RECORDS APP</a>
    </li>
  </ul>
</nav>
</template>

<script>
export default {
  methods: {
    changeMode() {
      this.$store.dispatch(`CHANGE_MODE`)
    },
  }
}
</script>

<style lang="stylus" scoped>
@import "../../styles/main"

.navigation {
    li {
        border-bottom: 1px solid lightgray;
        padding: 0.65rem;
        margin-bottom: 0;
    }
    a {
        text-decoration: none;
    }
    a.is-active {
        font-weight: bold;
    }
}

.navigation--offcanvas {
    li {
        padding: 0.65rem 2rem;
    }
}
.navigation--offcanvas.is-open {
    display: block;
}
.navigation--sidebar {
    display: none;
}


</style>



